
.progress-circle[data-v-7b584232] {
    font-size: 20px;
    margin: 20px;
    position: relative; /* so that children can be absolutely positioned */
    padding: 0;
    width: 5em;
    height: 5em;
    background-color: #F2E9E1;
    border-radius: 50%;
    line-height: 5em;
}
.progress-circle[data-v-7b584232]:after{
    border: none;
    position: absolute;
    top: 0.35em;
    left: 0.35em;
    text-align: center;
    display: block;
    border-radius: 50%;
    width: 4.3em;
    height: 4.3em;
    background-color: white;
    content: " ";
}
/* Text inside the control */
.progress-circle .text[data-v-7b584232] {
    position: absolute;
    height: 5em;
    width: 5em;
    text-align: center;
    color: #53777A;
    z-index: 2;
